.cart-parent {
  overflow-x: hidden;
  min-height: 100vh;
  background-color: rgb(235, 235, 235);
}

.cart-cont1 {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  overflow-x: hidden;
}

.cart-cont1 p {
  font-size: 30px;
  font-weight: 600;
}

.cart-cont1 .cart-cont {
  padding: 0px 20%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px 40px;
  margin-bottom: 200px;
}

.cart-foot {
  background-color: var(--pri-col);
  padding: 0px 20%;
  height: 100px;
  bottom: 0;
  right: 0;
  left: 0;
  position: fixed;
}

.cart-foot1 {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 100px;
}

.cart-foot1 .cart-tot {
  font-size: 24px;
  font-weight: 600px;
}

.cart-foot1 .cart-tot span:nth-child(1) {
  background-color: white;
  font-family: sans-serif;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}

.cart-foot1 .cart-tot span:nth-child(2) {
  background-color: white;
  font-family: sans-serif;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  padding-left: 10px;
  padding-right: 20px;
}

.c-home-icon1 {
  z-index: 120;
  cursor: pointer;
  height: 60px;
  width: 60px;
  position: fixed;
  bottom: 110px;
  right: 30px;
  background-color: var(--pri-col);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid white;
}

.c-home-icon1 .c-home-icon .c-home {
  padding-top: 5px;
  font-size: 40px;
  color: white;
}

.c-home-icon1 .c-home-icon:hover .c-home {
  color: black;
}

.porder {
  border: none;
  border-radius: 3px;
  width: 200px;
  height: 40px;
  font-size: 22px;
  font-weight: 600;
  cursor: pointer;
}

.porder:hover {
  color: white;
  background-color: black;
}