.n-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  height: 80px;
}
.n-nav .n-foodiko-logo {
  padding-top: 10%;
  width: 140px;
}
.n-home .n-link button {
  font-size: 20px;
  font-weight: 600;
  width: 100px;
  height: 35px;
  background-color: white;
  color: black;
  border-radius: 20px;
  border: none;
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}
.n-home .n-link button:hover {
  transform: scale(1.05);
  color: white;
  background-color: var(--pri-col);
}
