.bodylgn1 {
  background: linear-gradient(to right, #ff9900 50%, #1a1a1a 50%);
  height: 100vh;
}
.login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 80%;
}
.login-form {
  padding: 30px 30px 10px 30px;
  background-color: white;
  width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login .login-font {
  text-align: center;
  font-weight: 600;
  font-size: 24px;
}
.login .forgot-pwd {
  text-align: end;
  padding-bottom: 20px;
}
.login .sign-uplgn {
  padding-top: 20px;
  padding-bottom: 5px;
}
.login-form .input-fieldlgn {
  padding-top: 20px;
}
.login-form .input-fieldlgn input {
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 10px;
  background-color: #d9d9d9;
  border-radius: 30px;
  border: none;
  height: 35px;
  width: 250px;
  font-weight: 500;
  font-size: large;
}
.font {
  font-weight: 500;
  font-size: 19px;
}
.font1 {
  color: black;
  font-weight: 600;
  cursor: pointer;
}
.font1:hover {
  color: #ff9900;
  text-decoration: underline;
}
.login-form button[type="submit"] {
  font-size: 20px;
  font-weight: 520;
  width: 100px;
  height: 35px;
  background-color: #ff9900;
  color: white;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}
.login-form button[type="submit"]:hover {
  background-color: black;
  transform: scale(1.1);
}
.icon {
  float: right;
}
