.clist-cont {
  display: grid;
  grid-template-columns: 120px 200px 80px;
  align-items: center;
  gap: 20px;
  background-color: white;
  padding: 10px;
  box-shadow: 0px 0px 8px 3px rgba(0, 0, 0, 0.2);
}
.clist-cont .clist-img {
  display: flex;
  align-items: center;
}
.clist-cont .clist-img img {
  cursor: pointer;
  width: 120px;
  height: 120px;
  object-fit: cover;
  aspect-ratio: 1/1;
}
.clist-cont .clist-body .clist-name {
  font-size: 24px;
  font-weight: 600;
}
.clist-cont .clist-body .clist-price,
.clist-cont .clist-body1 .clist-tprice {
  font-size: 20px;
  font-weight: 500;
}
.clist-cont .clist-body {
  display: flex;
  flex-direction: column;
  justify-items: center;
  gap: 10px;
}
.clist-cont .clist-body .clist-count input {
  height: 24px;
  width: 50px;
  text-align: center;
}
.clist-cont .clist-body .clist-price span,
.clist-cont .clist-body1 .clist-tprice span {
  font-family: sans-serif;
  font-size: 20px !important;
  font-weight: 500 !important;
  margin-right: 2px;
}
.clist-cont .clist-body1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}
.clist-cont .clist-body1 div:nth-child(1) {
  align-self: flex-end;
  cursor: pointer;
}
.clist-cont .clist-body1 div .i-cart1 {
  color: var(--pri-col);
}
