.bodyrpwd1 {
  background: linear-gradient(to right, #ff9900 50%, #1a1a1a 50%);
  height: 100vh;
}
.resetpwd {
  height: 80%;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.resetpwd-form {
  padding: 40px;
  background: white;
  width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.resetpwd .resetpwd-font {
  text-align: center;
  font-weight: 600;
  font-size: 24px;
  margin-bottom: 5px;
}
.resetpwd-form .input-fieldrpwd {
  padding-top: 30px;
}
.resetpwd-form .input-fieldrpwd input {
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 10px;
  background-color: #d9d9d9;
  border-radius: 30px;
  border: none;
  height: 40px;
  width: 250px;
  font-weight: 500;
  font-size: large;
}
.font {
  font-weight: 500;
  font-size: 19px;
}
.btnrpwd button[type="submit"] {
  margin-top: 40px;
  font-size: 20px;
  font-weight: 500;
  width: 130px;
  height: 35px;
  background-color: #ff9900;
  color: white;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}
.btnrpwd button[type="submit"]:hover {
  background-color: black;
  transform: scale(1.1);
}
.icon {
  float: right;
}
