.bodysgn1 {
  background: linear-gradient(to right, #ff9900 50%, #1a1a1a 50%);
  height: 100vh;
}
.signup {
  height: 80%;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.signup-form {
  background: white;
  padding: 30px 30px 10px 30px;
  width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.signup .signup-font {
  text-align: center;
  font-weight: 600;
  font-size: 24px;
}
.signup .loginsgn {
  padding-top: 15px;
  padding-bottom: 3px;
}
.signup-form .input-fieldsgn {
  padding-top: 20px;
}
.signup-form .input-fieldsgn input {
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 10px;
  background-color: #d9d9d9;
  border-radius: 30px;
  border: none;
  height: 35px;
  width: 250px;
  font-weight: 500;
  font-size: large;
}
.font {
  font-weight: 500;
  font-size: 19px;
}
.font1 {
  color: black;
  font-weight: 600;
  cursor: pointer;
}
.font1:hover {
  color: #ff9900;
  text-decoration: underline;
}
.signup-form button[type="submit"] {
  margin-top: 30px;
  font-size: 20px;
  font-weight: 500;
  width: 130px;
  height: 35px;
  background-color: #ff9900;
  color: white;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}
.signup-form button[type="submit"]:hover {
  background-color: black;
  transform: scale(1.1);
}
.icon {
  float: right;
}
